import Vue from 'vue'
import Router from 'vue-router'
import HomePage from '@/components/HomePage'
import Loginpage from '@/components/Loginpage'
import Featurepage from '@/components/Featurepage'
import ContactPage from '@/components/ContactPage'
import Pricepage from '@/components/Pricepage'
import Blogpage from '@/components/Blogpage'
import ScheduleDemo from '@/components/ScheduleDemo'
import Investorspage from '@/components/Investorspage'
import InvestorsDocuments from '@/components/InvestorsDocuments'
import Article1 from '@/components/article1'
import InvestorsPresentations from '@/components/InvestorsPresentations'


/* eslint-disable */
Vue.use(Router)

export default new Router({
    mode: 'history',
    routes: [{
        path: '/',
        name: 'HomePage',
        component: HomePage
    },
    {
        path: '/loginpage',
        name: 'Loginpage',
        component: Loginpage
    },
    {
        path: '/featurepage',
        name: 'Featurepage',
        component: Featurepage
    },
    {
        path: '/contact_us',
        name: 'ContactPage',
        component: ContactPage
    },
    {
        path: '/blogpage',
        name: 'Blogpage',
        component: Blogpage
    },
    {
        path: '/pricepage',
        name: 'Pricepage',
        component: Pricepage
    },
    {
        path: '/scheduledemo',
        name: 'ScheduleDemo',
        component: ScheduleDemo,
        beforeEnter(to, from, next) {
            window.location.href = "https://demodesk.com/book/iq-dispatch-team/demo";
        }
    },
    {
        path: '/investorspage',
        name: 'Investorspage',
        component: Investorspage
    },
    {
        path: '/investorsdocuments',
        name: 'InvestorsDocuments',
        component: InvestorsDocuments
    },
    {
        path: '/investorspresentations',
        name: 'InvestorsPresentations',
        component: InvestorsPresentations
    },
    {
        path: '/article1',
        name: 'Article1',
        component: Article1
    },
    ]
})


